// AI Mixins

@mixin _ais {
  align-items: flex-start; }

@mixin _aie {
  align-items: flex-end; }

@mixin _aic {
  align-items: center; }

// Mixins without position

@mixin fdc {
  display: flex;
  flex-direction: column; }

@mixin fdr {
  display: flex;
  flex-direction: row; }

// Column mixins positioned

@mixin fdcjs {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }

@mixin fdcje {
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }

@mixin fdcjc {
  display: flex;
  flex-direction: column;
  justify-content: center; }

@mixin fdcjsb {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

@mixin fdcjsa {
  display: flex;
  flex-direction: column;
  justify-content: space-around; }

@mixin fdcjbl {
  display: flex;
  flex-direction: column;
  justify-content: baseline; }

@mixin fdcjse {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly; }

// Row mixins positioned

@mixin fdrjs {
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }

@mixin fdrje {
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

@mixin fdrjc {
  display: flex;
  flex-direction: row;
  justify-content: center; }

@mixin fdrjsb {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

@mixin fdrjsa {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

@mixin fdrjbl {
  display: flex;
  flex-direction: row;
  justify-content: baseline; }

@mixin frcjse {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly; }

